:root {
  --main-black-color: #141414;
  --main-white-color: #fff;
  --dark-green-color: #1e2827;
  --numeral-gray-color: rgba(20, 20, 20, .4);
  --text-gray-color: rgba(20, 20, 20, .5);
  color: #00f;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

body {
  font-family: DM Sans;
}

:root {
  --header-bg-color-light: #f9f9f9;
  --header-bg-color-dark: #111;
  --header-border-color-light: #000;
  --header-border-color-dark: #fff;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  max-width: 1440px;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 18px 0;
  transition: all .4s;
}

.curent-link {
  color: #111;
  background-color: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.curent-link.shoping-list:after {
  content: url("cart-black.e925fb9f.svg");
  width: 20px;
  height: 20px;
  margin-left: 4px;
}

.header-container {
  max-width: 375px;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.container {
  margin: auto;
  padding: 0 20px;
}

.switch-button {
  align-items: center;
  display: flex;
}

.header-nav {
  align-items: center;
  margin-right: auto;
  display: flex;
}

.header-list {
  display: none;
}

.logo img {
  width: 100px;
  height: 28px;
}

.logo {
  display: flex;
}

nav ul {
  align-items: center;
  list-style: none;
  display: flex;
}

nav li {
  margin-right: 20px;
}

.shoping-list {
  align-items: center;
  display: flex;
}

.shoping-list:after {
  content: url("cart.e995823f.svg");
  width: 20px;
  height: 20px;
  margin-left: 4px;
}

.mobile-menu {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 1;
  background: radial-gradient(circle at top, #4f2ee8 12%, #fff 150%);
  margin: -2px;
  transition: all .3s;
  position: fixed;
  top: 64px;
}

.menu-container-list {
  max-width: 768px;
  flex-direction: column;
  align-items: center;
  margin-top: 183px;
  display: flex;
  position: relative;
}

.menu-list {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.menu-list li a {
  text-decoration: none;
}

.menu-list-nav {
  letter-spacing: -.02em;
  text-transform: uppercase;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}

.svg-books {
  content: url("group-6.1160b41a.png");
  width: 100%;
  height: auto;
  z-index: 0;
  position: fixed;
  bottom: 0;
}

.header-btn-mobil-menu {
  stroke: #111;
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("align-left.2a33c908.svg");
  border: rgba(0, 0, 0, 0);
  padding: 0;
  line-height: 0;
  transition: all .2s;
  display: flex;
}

.header-btn-mobil-menu-close {
  width: 28px;
  height: 28px;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("x-close.a3e5df78.svg");
  border: rgba(0, 0, 0, 0);
  padding: 0;
  line-height: 0;
  transition: all .2s;
  position: absolute;
  top: 0;
  right: 0;
}

.header-btns-container {
  width: 28px;
  height: 28px;
  position: relative;
}

.dark-theme .header-btn-mobil-menu {
  background-image: url("align-left-dark.8921d7d0.svg");
}

.dark-theme .header-btn-mobil-menu-close {
  background-image: url("x-close-white.543dd0d7.svg");
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.theme-switcher button {
  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.theme-switcher {
  width: 40px;
  height: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 14px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 30px;
  width: 30px;
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: #4f2ee8;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4f2ee8;
}

input:checked + .slider:before {
  -webkit-transform: translateX(24px);
  -ms-transform: translateX(24px);
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center no-repeat;
  transform: translateX(14px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.burger-menu {
  cursor: pointer;
  display: none;
}

.burger-bar {
  width: 30px;
  height: 3px;
  stroke: #111;
  margin: 6px 0;
  display: block;
}

header.dark-theme > .header-container {
  background-color: #111;
  transition: background-color .4s;
}

