.category_item {
  height: 20px;
  letter-spacing: -.04em;
  text-align: left;
  color: rgba(17, 17, 17, .6);
  cursor: pointer;
  white-space: nowrap;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
  transition: color .5s cubic-bezier(.25, .46, .45, .94);
}

body.dark-theme .category_item, .category_list.dark-theme .category_item {
  color: rgba(255, 255, 255, .6);
}

@keyframes uppercaseAnimation {
  0% {
    text-transform: none;
    font-size: 16px;
  }

  30% {
    opacity: 0;
  }

  100% {
    text-transform: uppercase;
    font-size: 20px;
  }
}

@keyframes lowercaseAnimation {
  0% {
    text-transform: uppercase;
    font-size: 20px;
  }

  30% {
    opacity: 0;
  }

  100% {
    text-transform: none;
    font-size: 16px;
  }
}

.category_item {
  animation: lowercaseAnimation .5s cubic-bezier(.25, .46, .45, .94) forwards;
}

.category_item:hover {
  color: #4f2ee8;
  font-family: DM Sans;
  font-weight: 700;
  animation: uppercaseAnimation .5s cubic-bezier(.25, .46, .45, .94) forwards;
}

